/**
 * Create human readable time difference
 *
 * @export
 * @param {number} current in milliseconds
 * @param {number} previous in milliseconds
 * @return {string}  string
 */
export default function timeDifference (current: number, previous: number): string {
  const msPerMinute = 60 * 1000
  const msPerHour = msPerMinute * 60
  const msPerDay = msPerHour * 24
  const msPerMonth = msPerDay * 30
  const msPerYear = msPerDay * 365

  const elapsed = current - previous

  if (elapsed < msPerMinute / 2) {
    return ''
  }
  if (elapsed < msPerMinute) {
    return Math.round(elapsed / 1000) + ' '
  } else if (elapsed < msPerHour) {
    return Math.round(elapsed / msPerMinute) + ' '
  } else if (elapsed < msPerDay) {
    return Math.round(elapsed / msPerHour) + ' '
  } else if (elapsed < msPerMonth) {
    return ` ${Math.round(elapsed / msPerDay)} `
  } else if (elapsed < msPerYear) {
    return ` ${Math.round(elapsed / msPerMonth)} `
  }
  return ` ${Math.round(elapsed / msPerYear)} `
}
