import { Dialog, DialogActions, DialogContent, DialogTitle, Button } from '@material-ui/core'

import React from 'react'
import { SmartLink } from './Link'

const EditWarning = ():JSX.Element => {
  return (
    <>
      <p> OI Wiki </p>
      <p>
      
        <SmartLink to="/intro/htc/" target="_blank" rel="noopener noreferrer nofollow"></SmartLink>
      
      </p>
      <p></p>
    </>
  )
}

interface Props{
  relativePath: string;
  dialogOpen: boolean;
  setDialogOpen: (props:boolean)=> any;
  location: any;
}
const EditWarn: React.FC<Props> = (props: Props) => {
  const { relativePath, dialogOpen, setDialogOpen, location } = props
  const editURL = 'https://github.com/OI-wiki/OI-wiki/edit/master/docs/'

  return (
    <Dialog
      open={dialogOpen}
      onClose={() => {
        setDialogOpen(false)
      }}
    >
      <DialogTitle></DialogTitle>
      <DialogContent><EditWarning /></DialogContent>
      <DialogActions>
        <Button
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
        <Button
          component="a"
          href={editURL + relativePath}
          target="_blank"
          rel="noopener noreferrer nofollow"
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default EditWarn
